/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #677589;
	font-family: "Alegreya", serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

h2,
h3,
h4,
h5,
h6 {
	font-size: 1.2rem;
	clear: both;
	font-family: "Alegreya Sans", sans-serif;
	color: #002D62;
}

h1 {
	font-family: "Alegreya Sans", sans-serif;
	font-size: 1.3rem;
	clear: both;
	color: #002D62;
	text-align: center;
}

.h1 {
	font-family: "Alegreya Sans", sans-serif;
	font-size: 3rem;
	font-weight: bold;
}

.header-menu-wrapper,
.header-menu-wrapper button,
.footer-top,
.footer-bottom {
	font-family: "Alegreya Sans", sans-serif;
}

@media (min-width: 48em) {
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-size: 1.3rem;
	}
	h1 {
		font-size: 1.5rem;
	}
	.h1 {
		font-size: 4.5rem;
	}
}
p {
	margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

@media (min-width: 48em) {
	body,
	button,
	input,
	select,
	optgroup,
	textarea {
		font-size: 1.1rem;
	}
}
/* Elements
--------------------------------------------- */
body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #4169e1;
}
a:visited {
	color: #800080;
}
a:hover, a:focus, a:active {
	color: #191970;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
	display: grid;
	grid-template-columns: 1fr minmax(0, 1920px) 1fr;
	grid-template-areas: ". header ." ". main. " ". footer .";
	background-color: #F5FEFD;
}

.site-header {
	grid-area: header;
}

.site-main {
	grid-area: main;
}

.site-footer {
	grid-area: footer;
}

/* Added for Boldly Built */
/* ---------------------------------------------
	page wrapper
--------------------------------------------- */
.page-wrapper {
	margin: 10rem auto;
	margin-bottom: 3rem;
}

.home .page-wrapper,
.page-id-8 .page-wrapper,
.page-id-10 .page-wrapper {
	margin: 3rem auto;
}

/* ---------------------------------------------
	header, page wrapper, and footer
--------------------------------------------- */
.page-wrapper,
.header-menu-wrapper,
.footer-top,
.footer-bottom {
	padding: 0 1rem;
}

@media (min-width: 37.5em) {
	.page-wrapper {
		max-width: 90em;
	}
	.header-menu-wrapper,
	.footer-top,
	.footer-bottom {
		margin: 0 auto;
		max-width: 90em;
	}
}
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}
.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
	display: block;
	left: auto;
}
.main-navigation ul ul a {
	width: 200px;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
}
.main-navigation a {
	display: block;
	text-decoration: none;
}
/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

/* Added for Boldly Built */
.site-header {
	background-color: #677589;
	position: fixed;
	left: 0;
	right: 0;
	z-index: 999;
}

.header-menu-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	position: relative;
	left: 0;
	right: 0;
	z-index: 999;
}
.header-menu-wrapper svg {
	fill: #F5FEFD;
}
.header-menu-wrapper .custom-logo-link {
	width: 6rem;
}
.header-menu-wrapper .site-menu {
	display: flex;
	flex-direction: row-reverse;
}
.header-menu-wrapper button {
	background-color: transparent;
	border: none;
}
.header-menu-wrapper .menu-toggle {
	padding: 0.5rem 0 0.5rem 1rem;
}
.header-menu-wrapper .open-modal {
	border: 1px solid #F5FEFD;
	color: #F5FEFD;
	border-radius: unset;
	padding: 0.5rem 1rem;
}

.main-navigation {
	width: fit-content;
}

.main-navigation ul {
	position: absolute;
	top: 6.1rem;
	left: 0;
	right: 0;
	z-index: 999;
	background-color: #677589;
}
.main-navigation ul a {
	color: #F5FEFD;
	font-weight: 700;
	font-size: 1.5rem;
	display: block;
	padding: 0.75rem 1.25rem;
	text-align: center;
}

@media (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: flex;
		position: unset;
		background-color: unset;
	}
	.main-navigation ul a {
		font-size: 1.5rem;
	}
	.site-header .site-menu {
		flex-direction: row;
	}
}
/* Content
--------------------------------------------- */
.sticky {
	display: block;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.site-footer {
	background-color: #677589;
	color: #F5FEFD;
}
.site-footer ul {
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: space-between;
	text-transform: uppercase;
}
.site-footer ul a {
	text-decoration: none;
	color: #F5FEFD;
	font-size: 1.125rem;
}
.site-footer p {
	margin: 0;
	text-align: center;
}
.site-footer .footer-top .menu {
	justify-content: flex-start;
	gap: 1rem;
}
.site-footer .footer-bottom {
	display: flex;
	flex-direction: column-reverse;
	gap: 1rem;
}

.footer-top, .footer-bottom {
	padding-top: 2rem;
	padding-bottom: 2rem;
}

.footer-border {
	border-top: 1px solid #F5FEFD;
}

@media (min-width: 37.5em) {
	.site-footer .footer-bottom {
		flex-direction: row;
		justify-content: space-between;
	}
	.site-footer .footer-bottom .menu {
		gap: 1rem;
	}
}
/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

/* Pages
--------------------------------------------- */
