.step-heading {
	font-size: 3rem;
	text-align: center;
	text-transform: capitalize;
}

.step-icon,
.step-subheading,
.step-description {
	text-align: center;
}

.step-icon {
	background-color: #002D62;
	width: 60px;
	height: 60px;
	margin: 0 auto;
	border-radius: 20%;
}

.step-icon > p {
	color: #F5FEFD;
	font-size: 2rem;
	margin: 0;
}

.step-image {
	display: none;
}

@media (min-width: 48em) {
	.step-image {
		display: inline-block;
		height: 100%;
		max-width: 50%;
		border-radius: 10%;
		max-height: 250px;
		object-fit: cover;
	}
	.steps {
		display: grid;
		grid-template-columns: 1fr 1fr;
		column-gap: 1rem;
		justify-items: center;
		position: relative;
		margin-bottom: 2rem;
	}
}
@media (min-width: 64em) {
	#developmentStep {
		margin-bottom: 250px;
		padding-left: 2rem;
		padding-right: 2rem;
	}
	.steps {
		max-height: 250px;
	}
	.step-image {
		height: 400px;
		max-height: 400px;
		position: relative;
	}
	.step1 .step-image,
	.step3 .step-image {
		left: -10%;
	}
	.step2 .step-image,
	.step4 .step-image {
		right: -25%;
	}
	.step-icon {
		margin: 0;
	}
	.step-description,
	.step-subheading {
		text-align: start;
	}
	.step-subheading {
		font-size: 2rem;
		text-transform: uppercase;
	}
	.step-description {
		font-size: 1.25rem;
	}
	.steps > section {
		justify-self: start;
	}
	.step-heading {
		font-size: 5rem;
		width: 60%;
		margin: 1rem auto 2rem auto;
	}
}
.about-article {
	display: flex;
	flex-direction: column-reverse;
}
.about-article img {
	display: block;
	margin: 0 auto;
}
.about-article hr {
	border-top: solid 1px #677589;
}

@media screen and (min-width: 56.25em) {
	.about-message {
		text-align: center;
	}
	.about-article {
		display: block;
		flex-direction: unset;
		position: relative;
		max-width: 1000px;
		margin: 0 auto;
	}
	.about-article .floating-top {
		position: relative;
		display: flex;
		flex-direction: column;
		z-index: 2;
		margin-left: 33%;
		margin-bottom: 30%;
	}
	.about-article .floating-top p {
		padding: 10% 0;
		padding-left: 20%;
	}
	.about-article .floating-bottom {
		position: absolute;
		top: 0;
		display: flex;
		max-width: 40%;
	}
	.about-article img {
		max-width: 400px;
	}
	.about-article h2 {
		font-size: 2rem;
	}
	.about-article hr {
		width: 300px;
		margin: 0;
	}
}
#home-expertise p,
#home-expertise h2 {
	text-align: center;
}

.service-wrapper {
	display: flex;
	justify-content: center;
}

.service-block {
	display: inline-block;
	background-color: #475F9B;
	border-radius: 5%;
	margin: 1em;
}
.service-block img {
	height: 10em;
	width: 18em;
	border-radius: 5% 5% 0 0;
	object-fit: cover;
}
.service-block p {
	text-align: center;
	color: #F5FEFD;
}

.expertise-button {
	display: block;
	text-align: center;
}

#home-feature h2,
#home-feature h3,
#home-feature a {
	text-align: center;
}
#home-feature a {
	display: block;
}
#home-feature img {
	max-width: 90%;
	margin: 0 auto;
	display: block;
	border-radius: 5%;
}

.descr-box {
	background-color: #c9d3e3;
	padding: 3em;
	margin-top: 2em;
	border-radius: 5%;
}
.descr-box p {
	color: #002D62;
	line-height: 2.5em;
}

@media (min-width: 768px) {
	.feature-wrapper {
		display: grid;
		grid-template-columns: repeat(9, 1fr);
		grid-template-rows: repeat(8, 1fr);
		grid-column-gap: 0px;
		grid-row-gap: 0px;
	}
	.feature-wrapper img {
		grid-area: 3/1/7/5;
		z-index: 5;
	}
	.feature-wrapper .descr-box {
		grid-area: 2/4/8/10;
	}
	.feature-wrapper .descr-box p {
		margin: 0 0 0 5em;
		line-height: 2.5em;
	}
}
