	}
}
.banner {
	position: relative;
}
.banner img {
	display: block;
	height: 100vh;
	object-fit: cover;
	object-position: 60% 50%;
}

.page-id-8 .banner img {
	object-position: 50% 50%;
}

.page-id-10 .banner img {
	object-position: 20% 50%;
}

.post-thumbnail::after {
	content: "";
	position: absolute;
	background-color: rgba(0, 45, 98, 0.7);
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: 2;
}

.hero-text {
	position: absolute;
	top: 10%;
	height: fit-content;
	color: #F5FEFD;
	z-index: 3;
	padding-left: 1rem;
	padding-right: 1rem;
}

@media (min-width: 48em) {
	.hero-text {
		top: 0;
		max-width: 75%;
		padding-left: 2rem;
		padding-right: 2rem;
	}
}
@media (min-width: 64em) {
	.banner img {
		height: 75vh;
	}
	.hero-text {
		max-width: 60%;
	}
}
/* Modal
--------------------------------------------- */
.modal {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	padding: 2rem;
	width: 100%;
	visibility: hidden;
	opacity: 0;
	transition: all 0.3s ease;
}
.modal .modal-backdrop {
	background: rgba(0, 0, 0, 0.7);
	height: 190%;
	width: 195%;
	position: fixed;
}
.modal .modal-content {
	background: #fff;
	border-radius: 5px;
	transition: all 0.6s ease;
	z-index: 1000;
	position: absolute;
	top: 390%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.modal .modal-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #ccc;
	padding: 0.5rem;
}
.modal .modal-header h2 {
	margin: auto;
	padding: 0.5em 0;
	text-transform: uppercase;
}
.modal .modal-header .close-modal {
	font-size: 2rem;
	padding: 0;
	margin: 0;
	height: 30px;
	width: 50px;
	text-align: center;
}
.modal .modal-header .close-modal:hover {
	color: #000;
}
.modal .modal-body {
	padding: 0 1.5rem;
}
.modal.visible {
	opacity: 1;
	visibility: visible;
}

.wpcf7-form textarea {
	width: 100% !important;
	height: 8em;
}

/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/
/* Jetpack infinite scroll
--------------------------------------------- */
/* Hide the Posts Navigation and the Footer when Infinite Scroll is in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	display: none;
}

/* Re-display the Theme Footer when Infinite Scroll has reached its end. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
/* Accessibility
--------------------------------------------- */
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
}
.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #21759b;
	display: block;
	font-size: 0.875rem;
	font-weight: 700;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
	outline: 0;
}

/* Alignments
--------------------------------------------- */
.alignleft {
	/*rtl:ignore*/
	float: left;
	/*rtl:ignore*/
	margin-right: 1.5em;
	margin-bottom: 1.5em;
}

.alignright {
	/*rtl:ignore*/
	float: right;
	/*rtl:ignore*/
	margin-left: 1.5em;
	margin-bottom: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 1.5em;
}